.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
}

.card__logo {
  justify-content: center;
  margin-bottom: 40px;
  display: flex;
}

.logo {
  width: 160px;
  height: 134px;
}

.info__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.info__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.reviews {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.reviews__text {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.cost {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.cost__title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.cost__sum {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.buy-now {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border-style: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
}

.buy-now:hover {
  color: #00acdc;
  text-align: center;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

p, h2 {
  margin: 0;
  padding: 0;
}

